export default {
    /** ====================  ==================== */
    getQueryStringByName: function (name) {
        var result = document.location.search.match(new RegExp('[\?\&]' + name + '=([^\&]+)', 'i'));
        if (result == null || result.length < 1) {
            return '';
        }
        return result[1];
    },
    getCurrentPath (opts) {
        // var rtn = document.location.hash;
        // if (opts && opts.pathOnly) {
        //     rtn = rtn.split('?')[0].replace(/#!\//g, '');
        // }
        // return rtn;
        return document.location.pathname;
    },

    /** ====================  ==================== */
    /**
     * 
     *              -- Author by Dio Zhu. on 2017.5.2
     */
    getThumbnail (url) {
        if (!url) {
            return 'http://static1.systoon.com/share/img/185x185.png';
            // return '../static/images/default-avatar.png';
        }
        if (/qiniu.toon.mobi/.test(url)) { // 
            return url + '?imageView2/0/w/750';
        }
        // 
        if (url.indexOf('http://img.icon.systoon.com') >= 0 || url.indexOf('http://static1.systoon.com') >= 0) {
            return url;
        }
        if (url.match(/_/g) && url.match(/_/g).length > 1) return url; // 
        let w = 80, h = 80, q = 100,
            path = url.substr(0, url.lastIndexOf('.')),
            mimeType = url.substring(url.lastIndexOf('.'), url.length),
            suffix = '_' + w + '_' + h + '_' + q + '_1';
        if (mimeType.length > 4) { // feedIds_1948762391184096avatar'http://scloud.toon.mobi/f/Zs9xtlKKvwI6-iy1CGlAK5UFxlwMtlBU-U4tt0aqqYkfI'
            return path + mimeType + suffix + '.jpg';
        } else {
            return path + suffix + mimeType;
        }
    },
    getQiniuThumb (obj) {
        if (!obj || !obj.url) { return; };
        var mode = obj.mode || 2,
            w = obj.w || 650,
            h = obj.h || 200,
            newUrl = '';

        if (obj.w && obj.h) {
            newUrl = obj.url + '?imageView2/' + mode + '/w/' + w + '/h/' + h + '';
        } else if (obj.w) {
            newUrl = obj.url + '?imageView2/' + mode + '/w/' + w + '';
        } else if (obj.h) {
            newUrl = obj.url + '?imageView2/' + mode + '/h/' + h + '';
        } else if (!obj.w && !obj.h) {
            newUrl = obj.url + '?imageView2/' + mode + '/w/' + w + '';
        } else {
            newUrl = obj.url;
        };

        return newUrl;
    },

    /** ====================  ==================== */
    /**
     * 
     * time ,
     * format , yyyy MM dd hh mm ss'yyyy-MM-ddhh:mm','2014-06-1512:05'
     *
     * , logger.
     * , , ''.
     *              -- Modified by Dio Zhu. on 2016.10.20
     * iosnew Date(2016-10-31):invalid date...: 2016/10/31
     *              -- Modified by Dio Zhu. on 2016.11.01
     * */
    formatTime: function (time, format) {
        if (!time) {
            return '';
        }
        if (typeof time === 'string') {
            time = time.toString().replace(/-/g, '/'); // iosnew Date(2016-10-31):invalid date...: 2016/10/31
        }
        if (typeof time === 'number' && (time.toString().length) === 10) {
            time = parseInt(time + '000');
        }
        // 
        var stamp = new Date(time),
            year = stamp.getFullYear(),
            month = (stamp.getMonth() + 1) > 9 ? (stamp.getMonth() + 1) : '0' + (stamp.getMonth() + 1),
            day = stamp.getDate() > 9 ? stamp.getDate() : '0' + stamp.getDate(),
            hour = stamp.getHours() > 9 ? stamp.getHours() : '0' + stamp.getHours(),
            minute = stamp.getMinutes() > 9 ? stamp.getMinutes() : '0' + stamp.getMinutes(),
            sec = stamp.getSeconds() > 9 ? stamp.getSeconds() : '0' + stamp.getSeconds(),
            ms = stamp.getMilliseconds() < 100 ? '0' + (stamp.getMilliseconds() < 10 ? '0' + stamp.getMilliseconds() : stamp.getMilliseconds()) : stamp.getMilliseconds(),
            weeks = ['', '', '', '', '', '', ''],
            week = weeks[stamp.getDay()];
        if (format) {
            format = format.replace('yyyy', year);
            format = format.replace('MM', month);
            format = format.replace('dd', day);
            format = format.replace('hh', hour);
            format = format.replace('mm', minute);
            format = format.replace('ss', sec);
            format = format.replace('ms', ms);

            if (stamp.getDate() === new Date().getDate()) {
                week = '';
            }
            format = format.replace('week', week);
        } else {
            format = year + '-' + month + '-' + day + ' ' + hour + ':' + minute;
        }

        return format;
    },

    /**
     *  
     */
    dateToMs (date) {
        let endTimeHaoMiao = '';
        var dateTime = date.replace(/-/g, '/');
        endTimeHaoMiao = (new Date(dateTime)).getTime();
        return endTimeHaoMiao;
    },

    // 
    msToDay (ms) {
        let oDay = '', oHours = '';
        if (ms === '') {
            return ms;
        } else {
            oDay = parseInt(ms / 86400000);
            oHours = Math.round((ms % 86400000) / 3600000);
            if (oDay < 1) {
                return oHours + '';
            } else {
                return oHours === 0 ? oDay + '' : oDay + '' + oHours + '';
            }
        }
    },

    /**
     * 
     *              -- Author by Dio Zhu. on 2017.2.18
     */
    dateTrans (dt) {
        if (typeof dt === 'string') {
            dt = dt.toString().replace(/-/g, '/'); // iosnew Date(2016-10-31):invalid date...: 2016/10/31
        }
        if (typeof dt === 'number' && (dt.toString().length) === 10) {
            dt = parseInt(dt + '000');
        }
        dt = new Date(dt);
        return dt;
    },
    /**
     * 
     *              -- Author by Dio Zhu. on 2017.2.18
     */
    isSameYear (dt1, dt2) {
        if (!dt1 || !dt2) return false;
        dt1 = (dt1 instanceof Date) ? dt1 : this.dateTrans(dt1);
        dt2 = (dt2 instanceof Date) ? dt2 : this.dateTrans(dt2);
        return dt1.getFullYear() === dt2.getFullYear();
    },
    isSameMonth (dt1, dt2) {
        if (!dt1 || !dt2) return false;
        dt1 = (dt1 instanceof Date) ? dt1 : this.dateTrans(dt1);
        dt2 = (dt2 instanceof Date) ? dt2 : this.dateTrans(dt2);
        return dt1.getFullYear() === dt2.getFullYear() && dt1.getMonth() === dt2.getMonth();
    },
    isSameDay (dt1, dt2) {
        if (!dt1 || !dt2) return false;
        dt1 = (dt1 instanceof Date) ? dt1 : this.dateTrans(dt1);
        dt2 = (dt2 instanceof Date) ? dt2 : this.dateTrans(dt2);
        return dt1.getMonth() === dt2.getMonth() && dt1.getDate() === dt2.getDate();
    },

    /** ====================  ==================== */
    /**
     * validator
     *              -- Author By Dio Zhu. on 2017.5.10
     */
    validateEmail (val) {
        let re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/; // (-. )@(-)
        return re.test(val);
    },
    validateTel (val) {
        let re = /^0\d{2,3}-?\d{7,8}$/; // 02~3-7~8
        return re.test(val);
    },
    validateMobile (val) {
        let re = /^1\d{10}$/; // 111
        return re.test(val);
    }

};



