let getQueryStringByName = function (name) {
  let result = document.location.search.match(new RegExp('[\?\&]' + name + '=([^\&]+)', 'i'));
  if (result == null || result.length < 1) return '';
  return result[1];
};
let queryParams = {
  path: getQueryStringByName('path'),
  noId: getQueryStringByName('noId'),
  from: getQueryStringByName('from'),
  noType: getQueryStringByName('noType')
}

let config = {
  DEBUG: true,
  TITLE: '',
  APPID: 505,
  model: (/iphone|ipad|ipod/.test(navigator.userAgent.toLowerCase())) ? 'ios' : 'android',
  queryParams: queryParams,
  // code
  CODE: getQueryStringByName('code'), // toonCode
  userType: getQueryStringByName('param'), // userType
  ticket: getQueryStringByName('ticket') || '49b5af49c3c1608c3a372af5cfe5c580', // 
  // apiURL: 'http://172.31.63.3:8081',    // api
  // apiURL: 'http://172.28.50.96:8081',    // api
  apiURL: '',    // api
  LIMIT: 10,  // 

  history: {},    // 
  teacherType: 1, //   1  0 
  loginData: { // api.login
    userInfo: {userId: 0}
  },

  toonType: 100,

  COMMENT_UTL: 'http://172.28.50.173:8080',    // -: 
  COMMENT_APPID: 5,  // -: appid, id
  COMMENT_TOKEN: '',  // -: token, app, , , 
  COMMENT_LIMIT: 20,  // 
  COMMENT_MAX: 50,    // -500050

  BBS_URL: 'http://msbbs.toon.mobi',    // -: tokenurl/config/index.js
  BBS_APPID: 1,   // -: appid, id
  BBS_TOKEN: '',  // -: token, app, , , 
  BBS_LIMIT: 20,  // 
  BBS_MAX: 50,     // -: 500050

  ORG_URL: 'http://t100kaoqinapp.zhengtoon.com',    // 
  ORG_TOKEN: '',  // token

  AMAP_KEY: '7f339c48990ad1b3bac62a5338b92041',    // key
  AMAP_VERSION: '1.3' // API
};

if (process.env.NODE_ENV === 'production') { // . mod by Dio Zhu. on 2017.6.22
  config.URL = '';
  config.COMMENT_UTL = 'http://172.28.50.173:8080';
  config.BBS_URL = 'http://msbbs.toon.mobi';
}

export default config;



